            style="width: 100%"
            src="../images/cause/to.webp"
            alt=""
          />
          <router-link
            to="/home/cause"
            class="throw-link"
          >
            <img
              src="../images/cause/throw.webp"
              alt=""
            />
          </router-link>
        </div>
      </div>
    </transition>
  </div>
</template>
<script>
export default {
  data() {
    return {
      // true
      isShow: false,

      pos: 0,
      posUpdate: '',
      isshow_box: [false, false, false, false, false, false, false, false],

      data: [
        '48003100',
        '10',
        '19862021',
        '',
        '3000',
        ' 30004600',
        '44800-28002000',
        ''
      ]
    }
  },
  methods: {
    showTitle() {
      let partOne = this.$refs.partOne
      let catchTitle = partOne.getElementsByClassName('catch-title')[0]
      catchTitle.style.left = 0
      catchTitle.style.opacity = 1
      partOne
        .getElementsByClassName('aside-bottom')[0]
        .getElementsByTagName('img')[0].src = this.toImg(
        'images/cover/1.png'
      )
      this.isShow = true
    },
    toImg(img) {
      return require('../' + img)
    },
    getScrollPos() {
      this.pos = document.getElementsByTagName('html')[0].scrollTop
    },
    move() {
      const cHeight = document.querySelector('body').clientHeight
      const base = 1.7 * cHeight

      for (let i = 0; i < 8; i++) {
        if (!this.isshow_box[i] && this.pos > base + i * cHeight) {
          this.isshow_box[i] = true
        }
      }
    }
  },
  mounted() {
    this.posUpdate = setInterval(this.getScrollPos, 20)
  },
  unmounted() {
    clearInterval(this.posUpdate)
  },
  watch: {
    pos() {
      this.move()
    }
  }
}
</script>
<style scoped>
.home-layout {
  width: 100vw;
  height: 100vh;
}

/* part-one */
.part-one {
  width: 100%;
  height: 100%;
  position: relative;
}

.tip-bird {
  cursor: pointer;
  position: absolute;
  top: 0;
  left: 0;
  background-image: url('../images/2.png');
  background-size: 100% 100%;
  width: 11rem;
  height: 14rem;
}

.eight-screen {
  width: 100%;
  height: 80%;
  background-image: url('../images/cover/.webp');
  background-size: 100% 100%;
  background-repeat: no-repeat;
}

.catch-title {
  z-index: 50;
  position: absolute;
  top: 0;
  left: -100%;
  width: 70%;
  height: 80%;
  background-image: url('../images/intro/.png');
  background-size: 100% 100%;
  background-repeat: no-repeat;
  opacity: 0;
  transition: 1.5s ease-in-out;
}

.catch-title .x-gadget {
  transform: scaleX(1.5);
  margin: 3rem 0;
  height: 2rem;
  line-height: 2rem;
  text-align: center;
  font-size: 24px;
  background-image: linear-gradient(
    to bottom,
    rgb(226, 202, 147),
    rgb(216, 160, 30),
    black
  );
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
}

.catch-title .main-title {
  margin: 0 auto;
  margin-bottom: 2rem;
  width: 65%;
}

.catch-title .main-title img {
  width: 100%;
}

.catch-title .sub-title {
  margin: 0 auto;
  width: 80%;
  display: flex;
  justify-content: center;
}

.catch-title .sub-title img {
  width: 18rem;
  height: 4rem;
}

.aside-bottom {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 20%;
}

/* part-two */
.part-two {
  width: 100%;
  height: 100%;
  position: relative;
}

.full-block {
  margin: 5rem 0;
  width: 100%;
  height: 100%;
  position: relative;
}

.concat-block {
  width: 100vw;
  height: 100vh;
  background-size: cover;
  background-attachment: fixed;
}

.long-block {
  margin: 5rem 0;
  width: 100%;
  height: 300%;
  position: relative;
}

.throw-link {
  z-index: 2;
  position: absolute;
  left: 80%;
  top: 80%;
  animation: shine 4s linear infinite;
}
</style>
