<template>
  <div class="intro">
    <transition name="fade">
      <div
        v-if="isShow"
        class="membrane"
      ></div>
    </transition>
    <div class="aside-top">
      <img
        style="height: 50%"
        src="../images/gadget1.png"
        alt=""
      />
    </div>
    >
    <div
      class="bgimg"
      :style="
        'margin: 0 auto; width: 80%; height: 100vh;' +
        'background-image: url(' +
        require('../images/bgimg4.png') +
        ');'
      "
    >
      <div
        ref="introTrigger"
        class="trigger"
        @click="showIntroCard"
      >
        <img
          src="../images/-1c.png"
          alt=""
        />
      </div>
      <div ref="cardGroup">
        <div
          class="intro-card"
          style="top: 10%; left: 60%"
        >
          <img
            data-index="0"
            @click="showIntro"
            src="../images/0c.png"
            alt="..."
          />
        </div>
        <div
          class="intro-card"
          style="top: 30%; left: 15%"
        >
          <img
            data-index="1"
            @click="showIntro"
            src="../images/1c.png"
            alt="..."
          />
        </div>
        <div
          class="intro-card"
          style="top: 50%; left: 30%"
        >
          <img
            data-index="2"
            @click="showIntro"
            src="../images/2c.png"
            alt="..."
          />
        </div>
        <div
          class="intro-card"
          style="top: 60%; left: 70%"
        >
          <img
            data-index="3"
            @click="showIntro"
            src="../images/3c.png"
            alt="..."
          />
        </div>
      </div>
      <div
        ref="introBox"
        class="intro-box"
      >
        <span>
          <img
            class="pic-center"
            style="width: 55%"
            :src="require('../' + introList[introIndex].image)"
            alt=""
          />
        </span>
        <span>
          <span
            style="text-indent: 2em; margin-top: 10%; padding: 0 5%"
            class="psg-y"
          >
            {{ introList[introIndex].content }}
            <span
              class="jian-title-y back-button"
              @click="backToIntroCard"
            >
              
            </span>
          </span>
        </span>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      isShow: false,
      introList: [
        {
          image: 'images/0.jpg',
          content:
            ``
       },
        {
          image: 'images/1.jpg',
          content:
            `42`
        },
        {
          image: 'images/2.png',
          content:
          ``
        },
        {
          image: 'images/3.png',
          content:
            ` `
        }
      ],
      introIndex: 0
    }
  },
  methods: {
    showIntroCard() {
      const group = this.$refs.cardGroup.querySelectorAll('div')
      for (let i = 0; i < group.length; i++) {
        setTimeout(() => {
          group[i].style.opacity = 1
        }, 500 * i)
      }
      this.$refs.introTrigger.style.opacity = 0
      setTimeout(() => {
        this.$refs.introTrigger.style.zIndex = '-100'
      }, 1000)
      this.$refs.introTrigger.removeEventListener('click', this.showIntroCard)
    },
    showIntro(e) {
      this.isShow = true
      const box = this.$refs.introBox
      const spans = box.querySelectorAll('span')
      box.style.width = '90vw'
      // span show
      setTimeout(() => {
        for (let i = 0; i < spans.length; i++) {
          spans[i].style.display = 'inline-block'
          spans[i].style.opacity = '1'
        }
      }, 500)
      // change introContent
      const index = e.target.dataset.index
      this.introIndex = index
      e.preventDefault()
    },
    backToIntroCard() {
      this.isShow = false
      const box = this.$refs.introBox
      const spans = box.querySelectorAll('span')
      // span hide
      for (let i = 0; i < spans.length; i++) {
        spans[i].style.display = 'none'
        spans[i].style.opacity = '0'
      }
      // to 10
      setTimeout(() => {
        for (let i = 0; i < spans.length; i++) {
          box.style.width = '0'
        }
      }, 500)
    }
  }
}
</script>
<style scoped>
.intro {
  position: relative;
  width: 100vw;
}

.membrane {
  z-index: 1;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-image: url('../images/bgimg1.png');
  background-size: cover;
  background-repeat: no-repeat;
}

.bgimg {
  position: relative;
  margin-bottom: 20vh;
  width: 100%;
  height: 60vh;
  background-size: cover;
  background-repeat: no-repeat;
}

.aside-top {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  width: 100%;
  height: 20vh;
}

.trigger {
  cursor: pointer;
  position: absolute;
  opacity: 1;
  top: 100%;
  left: 50%;
  transform: translate(-50%, -100%);
  width: 20rem;
  height: 24rem;
  transition: 1s;
}

.trigger img {
  width: 100%;
  height: 100%;
}

.intro-card {
  cursor: pointer;
  position: absolute;
  opacity: 0;
  top: 0;
  left: 0;
  width: 8rem;
  height: 8rem;
  border-radius: 4rem;
  transition: 1s;
}

.intro-card img {
  width: 100%;
  height: 100%;
}

.intro-card img:hover {
  width: 100%;
  height: 100%;
  transform-origin: 50% 0;
  animation: swagger 1s linear infinite;
}

@keyframes swagger {
  25% {
    transform: rotateZ(-10deg);
  }

  50% {
    transform: rotateZ(0deg);
  }

  75% {
    transform: rotateZ(10deg);
  }

  100% {
    transform: rotateZ(0deg);
  }
}

.intro-box {
  z-index: 100;
  position: absolute;
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 0;
  /* width: 90vw; */
  height: 90vh;
  background-image: url('../images/bgimg3.png');
  background-size: 100% 100%;
  background-repeat: no-repeat;
  transition: 1s;
}

.intro-box > * {
  display: none;
  position: relative;
  opacity: 0;
  flex: 0 0 45%;
  margin: auto 10px;
  height: 85%;
  background-image: url('../images/bgimg2.png');
  background-size: 100% 100%;
  background-repeat: no-repeat;
  transition: 1s;
}

.back-button {
  cursor: pointer;
  position: absolute;
  right: 1%;
  bottom: 1%;
  width: 8rem;
  height: 3rem;
  line-height: 3rem;
  background-image: url('../images/button0.png');
  background-size: 100% 100%;
  background-repeat: no-repeat;
  transition: 0.5s;
  color: black;
}

.back-button:hover {
  background-image: url('../images/button1.png');
  color: rgb(139, 86, 2);
}
</style>
