<template>
  <div class="home-layout">
    <div
      ref="partOne"
      class="part-one"
      @click="showTitle()"
    >
      <transition name="right">
        <div
          v-if="!isShow"
          class="tip-bird"
        ></div>
      </transition>
      <div class="catch-title">
        <div
          class="x-gadget"
          style="margin-top: 5%"
        >
          
        </div>
        <p class="psg-y" style="text-indent: 2em;">
          ?,,,,,
        </p>
        <div class="x-gadget"></div>
      </div>
      <div class="eight-screen"></div>
      <div class="aside-bottom">
        <img
          style="height: 60%; transition: 1s"
          src="../images/cover/1_0.png"
          alt=""
        />
      </div>
    </div>
    <transition
      name="fade"
      mode="out-in"
    >
      <div
        v-if="isShow"
        class="part-two"
      >
        <div class="long-block">
          <div class="x-gadget">X</div>
          <div class="time-line">
            <div class="line"></div>
            <!-- 1 -->
            <img
              style="transform: translate(-19%, 0); width: 8rem"
              src="../images/cause/cause0.png"
              alt=""
            />
            <div
              data-index="0"
              class="card"
              style="top: 20%; left: -250%"
              @mouseenter="showCard"
            >
              <span
                :style="
                  'background-image: url(' +
                  require('../images/cause/0.webp') +
                  ');'
                "
              >
              </span>
              <span>
                <span class="psg-y">
                  <p v-if="introState[0]">
                    
                  </p>
                  <p v-else>
                    
                  </p>
                  <span
                    class="switch-button"
                    @click="switchPic"
                  >
                    >
                  </span>
                  <span
                    class="jian-title-y back-button"
                    @click="recoverCard"
                  >
                    
                  </span>
                </span>
              </span>
            </div>
            <div
              v-if="isCardTitleShow"
              data-index="0"
              class="card-title psg-y"
              style="top: 20%; left: 350%"
            >
              
            </div>
            <!-- 2 -->
            <img
              style="transform: translate(20%, 0); width: 8rem"
              src="../images/cause/cause1.png"
              alt=""
            />
            <div
              data-index="1"
              class="card"
              style="top: 40%; left: 350%"
              @mouseenter="showCard"
            >
              <span
                :style="
                  'background-image: url(' +
                  require('../images/cause/1.webp') +
                  ');'
                "
              >
              </span>
              <span>
                <span class="psg-y">
                  <p v-if="introState[1]">
                    
                  </p>
                  <p v-else>
                    
                  </p>
                  <span
                    class="switch-button"
                    @click="switchPic"
                  >
                    >
                  </span>
                  <span
                    class="jian-title-y back-button"
                    @click="recoverCard"
                  >
                    
                  </span>
                </span>
              </span>
            </div>
            <div
              v-if="isCardTitleShow"
              data-index="0"
              class="card-title psg-y"
              style="top: 40%; left: -250%"
            >
              
            </div>
            <!-- 3 -->
            <img
              style="transform: translate(-19%, 0); width: 8rem"
              src="../images/cause/cause2.png"
              alt=""
            />
            <div
              data-index="2"
              class="card"
              style="top: 60%; left: -250%"
              @mouseenter="showCard"
            >
              <span
                :style="
                  'background-image: url(' +
                  require('../images/cause/2.webp') +
                  ');'
                "
              >
              </span>
              <span>
                <span class="psg-y">
                  <p v-if="introState[2]">
                    4kaBP2.8kaBP
                  </p>
                  <p v-else>
                    4kaBP
                  </p>
                  <span
                    class="switch-button"
                    @click="switchPic"
                  >
                    >
                  </span>
                  <span
                    class="jian-title-y back-button"
                    @click="recoverCard"
                  >
                    
                  </span>
                </span>
              </span>
            </div>
            <div
              v-if="isCardTitleShow"
              data-index="0"
              class="card-title psg-y"
              style="top: 60%; left: 350%"
            >
              
            </div>
            <!-- 4 -->
            <img
              style="transform: translate(20%, 0); width: 8rem"
              src="../images/cause/cause3.png"
              alt=""
            />
            <div
              data-index="3"
              class="card"
              style="top: 80%; left: 350%"
              @mouseenter="showCard"
            >
              <span
                :style="
                  'background-image: url(' +
                  require('../images/cause/3.webp') +
                  ');'
                "
              >
              </span>
              <span>
                <span class="psg-y">
                  <p v-if="introState[3]">
                    
                  </p>
                  <p v-else>
                    
                  </p>
                  <span
                    class="switch-button"
                    @click="switchPic"
                  >
                    >
                  </span>
                  <span
                    class="jian-title-y back-button"
                    @click="recoverCard"
                  >
                    
                  </span>
                </span>
              </span>
            </div>
            <div
              v-if="isCardTitleShow"
              data-index="0"
              class="card-title psg-y"
              style="top: 80%; left: -250%"
            >
              
            </div>
            <!-- end -->
          </div>
          <div class="x-gadget">X</div>
        </div>
        <div class="full-block">
          <transition name="fade">
            <div
              v-if="showFinal"
              class="final-title psg-y"
            >
              <span style="margin-top: 20%; font-size: 25px">
                ,<br />
                
              </span>
            </div>
          </transition>
          <SunWheel
            @update="showNav"
            :isShow="go"
          />
          <div
            v-if="showFinal"
            class="bird-left"
          ></div>
          <div
            v-if="showFinal"
            class="bird-right"
          ></div>
        </div>
      </div>
    </transition>
  </div>
</template>
<script>
import SunWheel from './sunwheel.vue'
export default {
  components: {
    SunWheel
  },
  data() {
    return {
      // true
      isShow: false,
      posUpdate: '',
      pos: 0,
      go: false,
      showFinal: false,
      isCardTitleShow: true,
      introState: [true, true, true, true],
      firstShowCard: [false, false, false, false]
    }
  },
  methods: {
    showTitle() {
      let partOne = this.$refs.partOne
      let catchTitle = partOne.getElementsByClassName('catch-title')[0]
      catchTitle.style.left = 0
      catchTitle.style.opacity = 1
      partOne
        .getElementsByClassName('aside-bottom')[0]
        .getElementsByTagName('img')[0].src = this.toImg(
        'images/cover/1.png'
      )
      this.isShow = true
    },
    toImg(img) {
      return require('../' + img)
    },
    showNav() {
      this.$emit('update')
    },
    getScrollPos() {
      // 
      this.pos = document.getElementsByTagName('html')[0].scrollTop
      // 
      const win_height = document.querySelector('body').clientHeight

      // 
      // , 50vh
      const base = (100 / 100) * win_height
      const cards = document.querySelectorAll('.card')
      // dom
      if (cards.length === 0 && !this.isShow) {
        return
      }
      for (let i = 0; i < 4; i++) {
        if (
          this.pos > base + i * ((90 / 100) * win_height) &&
          !this.firstShowCard[i]
        ) {
          this.showCard(cards[i])
          this.firstShowCard[i] = true
        }
      }

      // 
      if (this.pos >= win_height * 4 && this.isShow && !this.go) {
        this.go = true
        setTimeout(() => {
          this.showFinal = true
        }, 7500)
      }
    },
    showCard(e) {
      // cardtitle
      this.isCardTitleShow = false
      // 
      const target = e.target === undefined ? e : e.target
      let index = target.dataset['index']
      target.style.left = '50%'
      target.style.width = '80rem'
      target.style.height = '32rem'
      target.style.padding = '2rem 1rem'
      // 
      const spans = target.querySelectorAll('span')
      // 
      spans[0].style.width = '68%'
      // 
      spans[1].style.display = 'inline-block'
      setTimeout(() => {
        spans[1].style.width = '30%'
      }, 200)
      // 
      setTimeout(() => {
        spans[1].getElementsByClassName('back-button')[0].style.opacity = 1
        spans[1].getElementsByClassName('switch-button')[0].style.opacity = 1
      }, 1000)
    },
    recoverCard(e) {
      // cardtitle
      this.isCardTitleShow = true
      // 
      e.target.style.opacity = 0
      // cardspan
      e.target.parentNode.parentNode.style.display = 'none'
      // 
      const target = e.target.parentNode.parentNode.parentNode
      let index = target.dataset['index']
      target.style.width = '30rem'
      target.style.height = '17rem'
      target.style.padding = '1rem 0.7rem'
      if (index % 2 != 0) {
        target.style.left = '350%'
      } else {
        target.style.left = '-250%'
      }
      // 
      const spans = target.querySelectorAll('span')
      // 
      spans[0].style.width = '100%'
      spans[0].style.backgroundImage =
        'url(' + require('../images/cause/' + String(index) + '.webp') + ')'
      // 
      spans[1].style.width = '0'
      e.preventDefault()
      // 
      spans[1].getElementsByClassName('back-button')[0].style.opacity = 0
    },
    switchPic(e) {
      // pass
      const target = e.target.parentNode.parentNode.parentNode
      const spans = target.querySelectorAll('span')
      let index = target.dataset['index']
      if (!this.introState[index]) {
        spans[0].style.backgroundImage =
          'url(' + require('../images/cause/' + String(index) + '.webp') + ')'
      } else {
        spans[0].style.backgroundImage =
          'url(' +
          require('../images/cause/' +
            String(index) +
            String(index) +
            '.webp') +
          ')'
      }
      this.introState[index] = !this.introState[index]
    }
  },
  mounted() {
    this.pos = 0
    this.go = false
    this.posUpdate = setInterval(this.getScrollPos, 20)
  },
  unmounted() {
    clearInterval(this.posUpdate)
  }
}
</script>
<style scoped>
.home-layout {
  width: 100vw;
  height: 100vh;
}

/* part-one */
.part-one {
  width: 100%;
  height: 100%;
  position: relative;
}

.tip-bird {
  cursor: pointer;
  position: absolute;
  top: 0;
  left: 0;
  background-image: url('../images/2.png');
  background-size: 100% 100%;
  width: 11rem;
  height: 14rem;
}

.eight-screen {
  width: 100%;
  height: 80%;
  background-image: url('../images/cause/toThis.webp');
  background-size: 100% 100%;
  background-repeat: no-repeat;
}

.catch-title {
  z-index: 50;
  position: absolute;
  top: 0;
  left: -100%;
  width: 70%;
  height: 80%;
  background-image: url('../images/intro/.png');
  background-size: 100% 100%;
  background-repeat: no-repeat;
  opacity: 0;
  transition: 1.5s ease-in-out;
}

.catch-title p {
  margin: 0 auto;
  width: 85%;
  font-size: 30px;
}

.aside-bottom {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 20%;
}

.part-two {
  width: 100%;
  height: 100%;
  position: relative;
}

.time-line {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  margin: 0 auto;
  width: 8rem;
  height: 100%;
}

.line {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 0.5rem;
  height: 100%;
  border-radius: 5px;
  background: rgb(237, 237, 131);
}

.long-block {
  width: 100%;
  height: 400%;
  position: relative;
}

.full-block {
  margin: 5rem 0;
  width: 100%;
  height: 100%;
  position: relative;
}
/* card */

.card {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  padding: 1rem 0.7rem;
  width: 30rem;
  height: 17rem;
  position: absolute;
  top: 0;
  left: 0;
  transform: translate(-50%, -50%);
  background-image: url('../images/cause/box0.png');
  background-size: 100% 100%;
  background-repeat: no-repeat;
  transition: 1s;
  overflow: hidden;
}

.card-title {
  width: 30rem;
  height: 10rem;
  text-align: center;
  position: absolute;
  top: 0;
  left: 0;
  font-size: 40px;
  font-weight: normal;
  transform: translate(-50%, -50%);
}
.card > span:first-child {
  position: relative;
  width: 100%;
  height: 100%;
  background-size: 100% 100%;
  background-repeat: no-repeat;
  transition: 1s;
}
.card > span:last-child {
  position: relative;
  display: none;
  width: 0;
  height: 100%;
  padding: 3rem 1rem;
  transition: 1s;
  background-image: url('../images/cause/box1.png');
  background-size: 100% 100%;
  background-repeat: no-repeat;
}

.switch-button {
  z-index: 100;
  cursor: pointer;
  opacity: 0;
  position: absolute;
  top: 50%;
  left: -10%;
  transform: translate(-50%, -50%);
  width: 8rem;
  height: 3rem;
  line-height: 3rem;
  text-align: center;
  font-size: 40px;
  font-weight: bold;
  transition: 0.5s;
  color: rgb(188, 72, 10);
  animation: l-r-jump 2s linear infinite;
}

@keyframes l-r-jump {
  25% {
    transform: translate(-45%, -50%);
  }
  50% {
    transform: translate(-50%, -50%);
  }
  75% {
    transform: translate(-55%, -50%);
  }
  100% {
    transform: translate(-50%, -50%);
  }
}

.back-button {
  z-index: 100;
  cursor: pointer;
  opacity: 0;
  position: absolute;
  right: 1%;
  bottom: 1%;
  width: 8rem;
  height: 3rem;
  line-height: 3rem;
  text-align: center;
  background-image: url('../images/cause/button0.png');
  background-size: 100% 100%;
  background-repeat: no-repeat;
  transition: 0.5s;
  color: black;
}

.back-button:hover {
  background-image: url('../images/cause/button1.png');
  color: rgb(139, 86, 2);
}

.final-title {
  width: 60%;
  margin: 0 auto;
  line-height: 2em;
  text-align: center;
  font-size: 40px;
  font-weight: normal;
}

.bird-left {
  position: absolute;
  opacity: 0;
  top: 30%;
  left: -30vw;
  transform: translate(-50%, -50%);
  width: 70rem;
  height: 40rem;
  background-image: url('../images/bird/birdLeft.png');
  background-size: 100% 100%;
  animation: left 2s ease-in-out forwards;
}

@keyframes left {
  80% {
    opacity: 0.2;
  }
  100% {
    opacity: 1;
    left: 0;
  }
}

.bird-right {
  position: absolute;
  opacity: 0;
  top: 30%;
  left: 130vw;
  transform: translate(-50%, -50%);
  width: 70rem;
  height: 40rem;
  background-image: url('../images/bird/birdRight.png');
  background-size: 100% 100%;
  animation: right 2s ease-in-out forwards;
}

@keyframes right {
  80% {
    opacity: 0.2;
  }
  100% {
    opacity: 1;
    left: 100%;
  }
}
</style>
